.TodoItem {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  padding: 15px;
  margin: 10px auto;
  border-radius: 20px;
  box-shadow: 0px 4px 8px rgb(32 35 41 / 15%);
  font-size: 0.9rem;
  color: #323232;
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 35px;
  width: 35px;
  font-size: 24px;
  font-weight: bold;
  /* background-color: #CCC; */
}
.container {
  display: flex;
  margin-top: 15px;
}

.Icon-check {
  border: 1px solid #323232;
  border-radius: 50%;
  margin-right: 10px;
}

.Icon-check--active {
  background-color: #06ff00;
}
.Icon-check:hover {
  background-color: #1ccf18ec;
}

.Icon-delete {
  border: 1px solid #323232;
  border-radius: 50%;
  margin-right: 10px;
}

.Icon-delete:hover {
  background-color: #ff1700;
  border-radius: 50%;
}

